.checklist {
    border-radius: 6px;
    padding: 30px;

    ul {
        color: $medium-grey;
        font-size: 14px;
        margin: 0;
        padding: 0;
        position: relative;
      }
      
      ul li {
        line-height: 1.3;
        list-style-type: none!important;
        padding-left: 26px;

        &:not(:last-child) {
          margin-bottom: 1em;
        }

        &::before {
          color: $bell-blue;
          content: $icon-check;
          font-family: $icomoon-font-family;
          left : -2px;
          position: absolute;
        }
      }

      .plaintext {
        margin-bottom: 15px;
      }
}