.modal {
    .modal-content {
        .modal-header {
            background: $greyish;
            padding: 22px 29px;
            .icon-close {
                color: $bell-blue;
                font-size: 20px;
            }
        }
        .modal-body {
            padding: 40px 30px;
            p:last-child {
                margin-bottom: 0;
            }
        }
    }
}