.alert {
    padding: 38px 52px 31px 90px;
    position: relative;

    .alert-heading {
        color: $black;
        font-size: 18px;
        margin-bottom: 12px;
    }

    .alert__body {
        color: $medium-grey;
        p {
            font-size: 14px;
            margin: 0;
            &:last-child {
                margin-bottom: 0;
            }
        }
        ul, ol {
            padding-top: 3px;

            li:not(:first-child) {
                margin-top: 10px;
            }
        }
    }

    &__icon {
        color: $bell-blue;
        font-size: 36px;
        // height: 40px;
        left: 28px;
        position: absolute;
        top: 28px;
        // width: 40px;

        // & + .alert__body {
        //     margin-top: 9px;
        // }
    }
}