.accordion {
    .card {
        border: none;

        &:not(:first-child) {
            border-top: 1px solid $grey;
        }

        .card-header {
            align-items: center;
            background-color: $white;
            border: none;
            cursor: pointer;
            display: flex;
            justify-content: space-between;
            // padding-left: 0;
            padding: 35px 0;

            // Change plus/minus icon when accordion close
            &[aria-expanded="false"] {
                span {
                    height: 42px;
                    transform: translateY(-30%);
                    width: 42px;
                    &::after {
                        color: $bell-blue;
                        content: $icon-expand-big;
                        font-family: $icomoon-font-family;
                        font-size: 42px;
                    }
                }
            }

            // plus/minus icon when accordion open
            &[aria-expanded="true"] {
                span {
                    height: 42px;
                    transform: translateY(-30%);
                    width: 42px;
                    &::after {
                        color: $bell-blue;
                        content: $icon-collapse-big;
                        font-family: $icomoon-font-family;
                        font-size: 42px;
                    }
                }
            }

            button {
                color: $black;
                font-family: $bell-slim-black;
                font-size: 24px;
                text-decoration: none;
                padding: 0;
                // padding: 35px 0;
            }

            i {
                color: $bell-blue;
            }
        }
        
        .card-body {
            color: $medium-grey;
            font-size: 14px;
            padding: 0 150px 0 0;
        }
    }
}

// Expand-small is actually collapse-small (same icon)
@media(max-width: 991px) {
    .accordion {
        .card {
            .card-header {
                &[aria-expanded="false"] {
                    span {
                        height: 36px;
                        width: 36px;
                        &::after {
                            content: $icon-expand-small;
                            font-size: 36px;
                        }
                    }
                }
                &[aria-expanded="true"] {
                    span {
                        height: 36px;
                        width: 36px;
                        &::after {
                            content: $icon-collapse-small;
                            font-size: 36px;
                        }
                    }
                }
            }
        }
    }
}