.interactive-legend {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 42px;

    &__image-section {
        position: relative;

        .interactive-legend__btn {
            background-color: $subtle-grey;
            border: 5px solid $light-blue;
            border-radius: 50%;
            box-shadow: 0 2px 10px 0 rgba(0,0,0,0.2);
            box-sizing: border-box;
            cursor: pointer;
            height: 45px;
            margin: -22.5px 0 0 -22.5px;
            position: absolute;
            transition: $default-animation;
            width: 45px;
            z-index: 1;

            &--active {
                background-color: $medium-blue;
                z-index: 2;
            }

            &:hover {
                background-color: $bell-blue;
            }
        }

        .interactive-legend__btn--small {
            height: 30px;
            margin: -15px 0 0 -15px;
            width: 30px;
        }
    }

    &__info-section {
        
    }

    &__info {
        background-color: $subtle-grey;
        padding: 35px 30px;

        p {
            color: $medium-grey;
            font-size: 14px;
            line-height: 18px;
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0;
            }
        }
        ol, ul {
            margin: 0;
            padding-left: 0;
            li {
                list-style-type: none;
                margin-bottom: 3px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    &__mobile {
        background-color: $medium-blue;
        border-radius: 10px 10px 0 0;
        bottom: 0;
        box-shadow: 0 2px 10px 0 $bell-blue;
        color: $white;
        height: 30vh;
        left: 0;
        overflow-x: hidden;
        position: fixed;
        transition: $default-animation;
        transform: translateY(110%);
        width: 100%;
        z-index: 55;

        &--open {
            transform: translateY(0);
        }

        &-closebtn {
            color: $light-blue;
            font-size: 30px;
            //font-weight: normal;
            position: absolute;
            right: 16px;
            text-decoration: none;
            top: 15px;

            &:hover {
                color: $white;
                text-decoration: none;
            }
        }
    }

    &__mobile-content {
        padding: 45px 40px 45px 15px;
        width: 100%;

        p {
            color: $white;
            font-size: 14px;
            line-height: 18px;
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0;
            }
        }
        ol, ul {
            margin: 0;
            padding-left: 0;
            li {
                list-style-type: none;
                margin-bottom: 3px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}

// Display problem from 768px to 991px
@media(max-width: 991px) {
    .interactive-legend {
        grid-template-columns: 1fr;
    }
    .interactive-legend__info-section {
        display: none;
    }
}