.hero {
    overflow-x: hidden;
    &__button {
        margin-top: 30px;
    }

    &__lead-paragraph {
        color: $black;
        font-size: 18px;
        margin: 0;
        padding: 0 65px 0 0;
    }

    &__image {
        max-width: calc(100% + 90px);
        transform: translateY(-16px);
        position: relative;
        left: -58px;
    }

    &__subhead {
        color: $bell-blue;
        font-weight: bold;
        margin: 0;
    }

    &__text {
        padding: 30px 18px 0px 0;
    }

    &__title {
        color: $black;
        margin: 0;
        padding: 13px 0;
    }

    .jumbotron {
        background-color: $white;
        @include gradient-y(rgba(232,232,232,0), rgba(223,223,223,.36));
        padding: 0 0 25px 0;
    }
}

@media(max-width: 767px) {
    .hero {
        .container {
            margin: 0;
            max-width: 100%;
        }
        &__image-section {
            .hero__image {
                left: 100%;
                max-width: 510px;
                transform: translateX(-100%);
                width: 100%;
            }
        }
        &__content-section {
            order: 2;
            .hero__text {
                margin: 30px 0 5px 0;

                h1 {
                    font-size: 26px;
                }
                .hero__lead-paragraph {
                    padding-right: 150px;
                }
            }
        }
    }
}