.site-header {
  box-shadow: 0 4px 6px 0 rgba(0,0,0,0.16);

  .navbar {
    min-height: 70px; // 4rem
    // Hide on desktop
    display: none;
  }

  &__section {
    color: $white;
    font-family: $bell-slim, serif;
    font-size: 22px;
    font-weight: bold;
    letter-spacing: -1.1px;
    line-height: 22px;
  }

  .nav-item {
    a {
      font-family: $bell-slim;
      font-size: 24px;
      font-weight: bold;
      margin-right: .8rem;
  
      &:hover, &:focus {
        color: #FFFFFF;
      }
  
      &::after {
        background: $white;
        content: '';
        display: block;
        height: 2px;
        margin-top: -4px;
        opacity: 0;
        width: 100%;
      }
    }
  
    .active {
      color: $white;
      font-family: $bell-slim-black;
      margin-top: 1.6px;
  
      &::after {
        opacity: 1;
      }
    }
  }

  .search-form {
    margin-right: 27px;
    position: relative;
    &__input {
      border: 0;
      border-radius: 18px;
      box-shadow: inset 2px 0 3px -1px rgba(0,0,0,.46);
      color: $black;
      font-size: 14px;
      padding: 7px 70px 7px 15px;
      &::placeholder {
        color: $light-grey-text;
      }
      &:focus {
        outline-offset: -2px;
      }
    }
    &__input-mobile {
      border: 0;
      color: $black;
      outline: none;
      padding: 10px 70px 10px 15px;
      &::placeholder {
        color: $light-grey-text;
      }
    }
    &__icon {
      background: transparent;
      border: none;
      position: absolute;
      top: 4px;
      right: 7px;
      i {
        color: $medium-blue;
        cursor: pointer;
        font-size: 24px;
      }
    }
    &__icon-mobile {
      background: transparent;
      border: none;
      color: $white;
      i {
        color: $medium-blue;
        cursor: pointer;
        font-size: 24px;
      }
    }
  }
  .navbar-toggler {
    i {
      color: $white;
      font-size: 24px;
      &.icon-close {
        font-size: 21px;
      }
      &.icon-search {
        font-size: 25px;
      }
    }
    &.searchbar-toggler {
      padding: 7px;
      &[aria-expanded='true'] {
        background: $medium-blue;
      }
    }
  }
  .search-form--mobile {
    display: grid;
    grid-template-columns: 97% 3%;
  }
  button {
    outline: none;
  }

  .close-menu {
    display: none;
  }
}

.search-section {
  position: absolute;
  right: 60px;
  top: 30px;
  z-index: 2;

  &.collapse:not(.show) {
    display: block;
  }

  .search-form {
    position: relative;
    &__input-desktop {
      border: 2px solid $grey;
      border-radius: 18px;
      color: $black;
      font-size: 14px;
      min-width: 260px;
      padding: 7px 48px 7px 15px;
      &::placeholder {
        color: $light-grey-text;
      }
      &:focus {
        border: 2px solid #96B8EF;
        outline: none;
      }
    }
    &__icon {
      background: transparent;
      border: none;
      position: absolute;
      right: 12px;
      top: 8px;
      i {
        color: $bell-blue;
        cursor: pointer;
        font-size: 24px;
      }
    }
    .icon-close {
      color: $bell-blue;
      cursor: pointer;
      display: none;
      font-size: 12px;
      position: absolute;
      right: 48px;
      top: calc(50% - 6px);
      &.show {
        display: block;
      }
    }
    .loading-spinner {
      animation: rotate 2.5s infinite;
      display: none;
      position: absolute;
      right: 74px;
      top: 9px;
      width: 21px;
      &.show {
        display: block;
      }
      @keyframes rotate {
        0% {transform: rotate(0);}
        100% {transform: rotate(720deg);}
      }
    }
    // remove default clear button
    /* clears the 'X' from Internet Explorer */
    input[type=search]::-ms-clear {  display: none; width : 0; height: 0; }
    input[type=search]::-ms-reveal {  display: none; width : 0; height: 0; }
    /* clears the 'X' from Chrome */
    input[type="search"]::-webkit-search-decoration,
    input[type="search"]::-webkit-search-cancel-button,
    input[type="search"]::-webkit-search-results-button,
    input[type="search"]::-webkit-search-results-decoration { display: none; }
  }

  &__box {
    background-color: $white;
    border: 1px solid $light-grey;
    border-radius: 12px;
    box-shadow: 0 14px 36px 0 rgba(0,0,0,0.3);
    display: block;
    opacity: 0;
    overflow-y: auto;
    padding: 35px 17px 20px 15px;
    position: absolute;
    right: 0;
    top: 55px;
    transition: $default-animation;
    visibility: hidden;
    width: 545px;

    &::after {
      content: '';
      border-left: 14px solid transparent;
      border-right: 14px solid transparent;
      border-bottom: 14px solid $white;

      bottom: 100%;
      height: 0;
      pointer-events: none;
      position: absolute;
      right: 15%;
      width: 0;
      z-index: 2;
    }

    .box-inner {
      max-height: 460px;
      overflow-y: auto;
      width: 100%;
      &::-webkit-scrollbar {
        width: 6px;
      }
      &::-webkit-scrollbar-track {
        background: $light-grey;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: $bell-blue;
        border-radius: 3px;
      }
    }

    .result-section {
      margin-right: 15px;
      &:not(:first-child) {
        border-top: 1px solid $light-grey;
        padding-top: 20px;
      }

      &__title {
        font-size: 16px;
        font-family: Arial, serif;
        font-weight: bold;
        margin: 0 0 12px 15px;
      }

      ul {
        padding-left: 0;
      }

      .result {
        cursor: pointer;
        list-style-type: none;

        &:hover {
          background-color: $subtle-grey;
          border-radius: 4px;
        }
        a {
          color: $medium-grey;
          display: grid;
          grid-template-columns: 25% 75%;
          &:hover {
            text-decoration: none;
          }
        }
        &__subsection {
          color: $medium-grey;
          font-size: 14px;
          padding: 7px 7px 7px 15px;
        }
        &__title {
          border-left: 1px solid $light-grey;
          padding: 7px 0 7px 23px;

          .icon-arrow-right {
            color: $bell-blue;
            font-size: 12px;
            font-weight: bold;
            margin: 0 10px;
          }

          small {
            color: $medium-grey;
            display: block;
            font-size: 14px;
          }
        }
      }

      span.highlight {
        color: $bell-blue;
      }
    }

    &.show {
      opacity: 1;
      visibility: visible;
    }
  }
}

@media(max-width: 991px) {
  .site-header {
    .navbar {
      display: flex;
    }
    .brand-section {
      display: flex;
      align-items: flex-end;
    }

    .navbar-brand {
      display: flex;
      padding-right: 0 !important;

      span {
        color: $white;
        font-family: $bell-slim, serif;
        font-size: 22px;
        font-weight: bold;
        letter-spacing: -1.1px;
        line-height: 22px;
        margin-left: 15px;
      }
    }

    .nav-link-mobile {
      color: $white;
      font-family: $bell-slim;
      padding: 0 0 2px 0;
    }
  }

  .search-section {
    left: 0;
    position: fixed;
    top: 70px;
    width: 100%;
    &.collapse:not(.show) {
      display: none;
    }
    .search-form {
      input {
        border: none;
        border-radius: 0;
        font-size: 16px;
        padding: 14px 48px 14px 15px;
        width: 100%;
        &:focus {
          border: none;
          outline: $bell-blue solid 1px;

        }
      }
      &__icon {
        top: 13px;
      }
      .loading-spinner {
        top: 16px;
      }
    }
    &__box {
      border-radius: 0;
      height: 100vh;
      padding: 20px 15px 20px 0;
      top: 52px;
      width: 100%;
      .box-inner {
        max-height: 100vh;
      }

      .result-section {
        .result {
          a {
            grid-template-columns: 30% 70%;
          }
          &__subsection {
            font-size: 12px;
          }
          &__title {
            font-size: 14px;
          }
        }
      }
    }
  }
}