a.download {
    display: block;
    text-decoration: none;
    div[class^="col"] & {
        height: 100%;
        .card {
            height: inherit;
        }
    }
}

.card--download {
    border: none;
    line-height: 1;
    margin: pxToRem(30px) 0;

    .card-body {
        align-items: center;
        border: 1px solid #E1E1E1;
        border-radius: 0.375rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;

        .card__prologue {
            font-family: $bell-slim, sans-serif;
            letter-spacing: -1px;
        }

        .card-title {
            font-family: $bell-slim-black, sans-serif;
            font-size: 40px;
            letter-spacing: -2px;
        }

        .card__product {
            font-family: $bell-slim, sans-serif;
            font-size: 40px;
            letter-spacing: -2px;
        }
    
        .card-text {
            font-family: $bell-slim, sans-serif;
        }

        .card__download-icon {
            bottom: -4px;
            color: $bell-blue;
            font-size: 14px;
            font-weight: bold;
            position: absolute;
            right: 15px;
    
            p {
                align-items: center;
                display: flex;
                justify-content: center;
            }
            .icon-download {
                background-color: $bell-blue;
                border-radius: 50%;
                color: $white;
                font-size: 30px;
                margin-left: 15px;
                padding: 5px;
            }
            .icon-download-o {
                color: $bell-blue;
                font-size: 26px;
            }
        }
    }

    div[class^="col"] & {
        height: 100%;
        margin: 0;
    }
}

.card__caption { 
    color: $medium-grey;
    margin-top: pxToRem(10px);
    p {
        font-size: 12px;
    }
}

.documentation__section > .card__caption {
    margin-top: -15px;
}

.download--no-image {
    .card-body {
        padding: 0;
    }
    .card__download-icon {
        position: static;
        p {
            font-size: 14px;
            justify-content: flex-end;
            margin: 0 30px 0 0;
        }
    }
}
