.page-header {
    &__gradient {
        background: linear-gradient(0deg, rgba(244,244,244,.5) 8%, rgba(255,255,255,0) 20%);
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
        width: 100%;
    }

    h1 {
        margin-bottom: 0;
    }

    &--no-img {
        padding-top: pxToRem(119px);
    }

    &--with-img {
        background-position: center center;
        background-repeat: no-repeat;
        // background-size: cover;
        height: 320px;
        position: relative;

        .page-header__text {
            position: absolute;
            bottom: pxToRem(30px);
        }
    }

    &__hamburger {
        display: none;
    }
    @include media-breakpoint-down(sm) {
        padding-top: 30px;
    }
}

@media(max-width: 991px) {
    .page-header {
        position: relative;
        
        &__hamburger {
            background-color: transparent;
            border: none;
            color: $bell-blue;
            display: block;
            position: absolute;
            top: 15px;

            &:focus {
                outline: none;
            }
        }
    }
}