// Define bell-slim font
@font-face {
  font-family: 'bell-slim';
  src: url('../fonts/bellslim_regular-webfont.eot');
  src: url('../fonts/bellslim_regular-webfont.eot?#iefix') format('embedded-opentype'),
  url('../fonts/bellslim_regular-webfont.woff') format('woff'),
  url('../fonts/bellslim_regular-webfont.ttf') format('truetype');
  font-weight: 200;
  font-style: normal;
}

@font-face {
  font-family: 'bell-slim';
  src: url('../fonts/bellslim_medium-webfont.eot');
  src: url('../fonts/bellslim_medium-webfont.eot?#iefix') format('embedded-opentype'),
  url('../fonts/bellslim_medium-webfont.woff') format('woff'),
  url('../fonts/bellslim_medium-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'bell-slim';
  src: url('../fonts/bellslim_semibold-webfont.eot');
  src: url('../fonts/bellslim_semibold-webfont.eot?#iefix') format('embedded-opentype'),
  url('../fonts/bellslim_semibold-webfont.woff') format('woff'),
  url('../fonts/bellslim_semibold-webfont.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}

// Bell Slim Black
@font-face {
  font-family: 'bellslim_font_black';
  src: url('../fonts/bellslim_black-webfont.woff') format('woff'), url('../fonts/bellslim_black-webfont.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

$bell-slim: 'bell-slim', Helvetica, Arial, sans-serif;
$bell-slim-black: 'bellslim_font_black', Helvetica, Arial, sans-serif;
