  font-family: "bell-slim", Helvetica, Arial, sans-serif, sans-serif;
  font-size: 24px;
  font-weight: bold;
  height: 80px;
  justify-content: center;
  margin-bottom: 15px;
  padding-left: 30px;
}

.swatch__colour--border {
  border: 1px solid #E1E1E1;
}

.swatch__name {
  color: #555555;
  font-size: 0.875rem;
  font-weight: bold;
}

.swatch__text {
  color: #555555;
}

.swatch__text p {
  font-size: 14px;
}

.swatch__note p {
  color: #555555;
  font-size: 14px;
}

.swatch__note p a {
  text-decoration: underline;
}

.swatch--large .swatch__colour {
  align-items: center;
  border-radius: 10px;
  font-family: 'Arial Bold', sans-serif;
  height: 154px;
  padding-left: 0;
}

.swatch .dark-text {
  color: #555555;
}

.table > thead > tr > th {
  padding: 1rem;
  border-color: #00549A;
  border-right-color: #00215E;
}

.table > thead > tr > th:last-child {
  border-right-color: #00549A;
}

.table > thead > tr > th,
.table > tbody > tr > td,
.table > tbody > tr > th {
  font-size: 14px;
  vertical-align: middle;
}

.nav-tabs {
  padding-bottom: 0;
  display: inline-block;
}

.nav-tabs .nav-item {
  display: inline-block;
}

.nav-tabs .nav-item:not(:last-child) {
  margin-right: 30px;
  margin-top: 0;
}

.nav-tabs .nav-item::before {
  content: '';
}

.nav-tabs .nav-link {
  align-items: center;
  color: #111111;
  display: inline-flex;
  flex-direction: column;
  padding: 15px 0;
  font-size: 18px;
  position: relative;
  text-decoration: none;
}

.nav-tabs .nav-link:hover {
  color: #111111;
}

.nav-tabs .nav-link::before {
  content: attr(data-text);
  content: attr(data-text)/"";
  height: 0;
  visibility: hidden;
  overflow: hidden;
  user-select: none;
  pointer-events: none;
  font-weight: bold;
}

@media speech {
  .nav-tabs .nav-link::before {
    display: none;
  }
}

.nav-tabs .active {
  font-weight: bold;
}

.nav-tabs .active::after {
  background-color: #00549A;
  bottom: 1px;
  content: '';
  display: block;
  height: 4px;
  position: absolute;
  width: 100%;
}

.nav-tabs > .nav-item > .nav-link {
  background: transparent;
  border: none;
}

.nav-tabs > .nav-item > .nav-link:hover::after, .nav-tabs > .nav-item > .nav-link:focus::after {
  background-color: #00549A;
  bottom: 1px;
  content: '';
  display: block;
  height: 4px;
  position: absolute;
  width: 100%;
}

.priority-nav {
  border-bottom: 1px solid #E1E1E1;
  white-space: initial;
}

@media (max-width: 767.98px) {
  .priority-nav {
    border-bottom: none;
  }
}

.priority-nav.is-open .icon-arrow-down {
  top: 5px;
  transform: rotate(180deg);
}

.priority-nav__wrapper {
  font-size: 18px;
  line-height: 26px;
  padding-bottom: 18px;
  padding-left: 61px;
  padding-right: 40px;
  position: relative;
}

.priority-nav__wrapper .icon-arrow-down {
  color: #00549A;
  font-size: 9px;
  right: 20px;
  pointer-events: none;
  position: absolute;
  top: 7px;
}

.priority-nav__wrapper::before {
  background: #D4D4D4;
  content: '';
  display: block;
  height: 24px;
  left: 41px;
  position: absolute;
  top: 0;
  width: 1px;
}

.priority-nav__wrapper[aria-haspopup="false"] .icon-arrow-down {
  display: none;
}

.priority-nav__wrapper[aria-haspopup="false"]::before {
  display: none;
}

.priority-nav__wrapper.active button {
  color: #111111;
  overflow: hidden;
  text-overflow: ellipsis;
}

.priority-nav__wrapper.active::after {
  background: #00549A;
  bottom: 0px;
  content: '';
  display: block;
  height: 4px;
  left: 66px;
  position: absolute;
  width: 90px;
}

.priority-nav__wrapper button {
  background: transparent;
  border: none;
  color: #00549A;
  position: relative;
  text-transform: capitalize;
  width: 76px;
  white-space: nowrap;
}

.priority-nav__wrapper button:focus {
  outline: none;
}

.priority-nav__dropdown {
  background: #fff;
  border: 1px solid #E1E1E1;
  box-shadow: 0 14px 36px 0 rgba(0, 0, 0, 0.3);
  list-style: none;
  padding: 10px;
  left: 50%;
  top: calc(100% + 20px);
  transform: translateX(-50%);
  width: 400px;
  z-index: 9999;
}

.priority-nav__dropdown a {
  border-radius: 3px;
  color: #111111;
  line-height: 26px;
  font-size: 16px;
  padding: 8px 10px;
}

.priority-nav__dropdown a:hover {
  background: #E1E1E1;
}

.priority-nav__dropdown a[aria-selected="true"] {
  font-weight: bold;
  text-decoration: underline;
}

.priority-nav__dropdown::before {
  content: '';
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 15px solid #dee2e6;
  bottom: 100%;
  height: 0;
  left: 50%;
  pointer-events: none;
  position: absolute;
  transform: translateX(-50%);
  width: 0;
  z-index: 2;
}

.priority-nav__dropdown::after {
  content: '';
  border-left: 14px solid transparent;
  border-right: 14px solid transparent;
  border-bottom: 14px solid #fff;
  bottom: 100%;
  height: 0;
  left: 50%;
  pointer-events: none;
  position: absolute;
  transform: translateX(-50%);
  width: 0;
  z-index: 2;
}

@media (max-width: 767.98px) {
  .priority-nav__wrapper {
    border-bottom: none;
    padding: 0;
  }
  .priority-nav__dropdown-toggle {
    display: none;
  }
  .priority-nav__dropdown {
    border: none;
    box-shadow: none;
    left: unset;
    margin-top: -51px;
    padding: 0 0 25px 0;
    position: relative;
    transform: none;
    visibility: visible;
    width: auto;
    z-index: 1;
  }
  .priority-nav__dropdown::before {
    border: none;
  }
  .priority-nav__dropdown::after {
    background: #E1E1E1;
    border: none;
    bottom: 0;
    content: '';
    height: 1px;
    left: 0;
    position: absolute;
    width: 204%;
  }
  .priority-nav__dropdown .nav-item:not(:last-child) {
    margin-bottom: 15px;
  }
  .priority-nav__dropdown .nav-item .nav-link {
    color: #00549A;
    display: inline;
    font-size: 14px;
    line-height: 26px;
    padding: 0;
    text-decoration: underline;
  }
  .priority-nav__dropdown .nav-item .nav-link:hover {
    background: transparent;
    color: #003778;
  }
  .priority-nav__dropdown .nav-item .nav-link[aria-selected="true"] {
    color: #111111;
    font-weight: bold;
    text-decoration: none;
  }
}
