.changelog {
    width: calc(100% - 300px);

    &__container {
        display: grid;
        grid-template-columns: 90% 10%;
        padding: 119px 0 60px 0;
        @include media-breakpoint-down(sm) {
            margin: 0 15px 0;
        }
    }

    &__title {
        span {
            font-size: 14px;
        }
        h1 {
            margin-bottom: 40px;
            margin-top: 4px;
        }
    }

    &__entry {
        padding-bottom: 60px;

        &::before {
            content: '';
            display: block;
            height: 30px;
            margin-top: -30px;
            visibility: hidden;
        }
        
        &:first-child {
            margin-top: 45px;
        }
        &:not(:first-child) {
            margin-top: 60px;
        }
        &:not(:last-child) {
            border-bottom: 2px solid $light-grey;
        }
        &:last-child {
            padding-bottom: 0;
        }

        h4 {
            margin: 30px 0 15px 0;
        }

        p {
            color: $medium-grey;
            margin: 16px 0 30px 0;
        }

        ul {
            font-size: 14px;
            list-style: none;
            padding-left: 12px;
            
            li {
                color: $medium-grey;
                line-height: 18px;
                padding-left: 30px;
                position: relative;
                &::before {
                    color: $medium-grey;
                    content: '\2022';
                    font-size: 18px;
                    position: absolute;
                    left: 2px;
                }
                &:not(:first-child) {
                    margin-top: 15px;
                }
                > ul {
                    margin-top: .5em;
                    position: relative;
                    li::before {
                        color: $medium-grey;
                        content: '-';
                        position: absolute;
                    }
                }
            }
        }
    }

    &__pages {
        color: $bell-blue;
        display: flex;
        margin-top: 44px;
    }
    &__previous {
        justify-content: flex-start;
        &::before {
            color: $bell-blue;
            content: $icon-arrow-left;
            display: inline-block;
            font-family: $icomoon-font-family;
            margin-right: 10px;
        }
    }
    &__next {
        justify-content: flex-end;
        &::after {
            color: $bell-blue;
            content: $icon-arrow-right;
            display: inline-block;
            font-family: $icomoon-font-family;
            margin-left: 10px;
        }
    }

    .spy-changelog {
        position: relative; // required for scrollspy
        height: auto;
        // overflow-y: scroll;
    }

    &__tableOfContents {
        margin-left: 42px;
        .list-group {
            border-left: 1px solid $light-grey;
            position: fixed;
            top: 41%;

            a {
                font-size: 14px;
                padding-left: 18px;
                &:hover {
                    color: $black;
                    &::after {
                        background-color: $bell-blue;
                        left: 0;
                        content: '';
                        display: block;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        width: 4px;
                    }
                }

                // Active class not working atm (spyscroll)
                &.active {
                    color: $black;
                    &::after {
                        background-color: $bell-blue;
                        left: 0;
                        content: '';
                        display: block;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        width: 4px;
                    }
                }
            }
        }
    }
}

@media(max-width: 991px) {
    .changelog {
        margin-left: 0;
        width: 90%;

        &__entry {
            &::before {
                height: 100px;
                margin-top: -100px;
            }
        }
    }
}