.documentation-footer {
    margin-left: 300px;
    padding: 40px 47px 42px 47px;

    @include media-breakpoint-down(md) {
        padding: 40px 15px 42px 15px;
    }

    p {
        color: $medium-grey;
        font-size: 12px;

        &:last-child {
            margin-bottom: 0;
        }

        a {
            color: $bell-blue;
            text-decoration: underline;
        }
    }
}

@media(max-width: 991px) {
    .documentation-footer {
      margin-left: 0;
    }
  }