.font-specs {
    &__title {
        margin-bottom: 0;
    }
    &__paragraph {
        color: $medium-grey;
        font-size: 14px;
        line-height: 18px; 
        margin-top: 15px;
    }
    &__container {
        border: 1px solid $grey;
        margin-top: 30px;
    }
    &__image-section {
        align-items: center;
        border-bottom: 1px solid $grey;
        display: flex;
        justify-content: center;
        padding: 40px 20px;
    }
    &__text-section {
        color: $medium-grey;
        font-size: 14px;
        line-height: 18px;
        padding: 30px 40px;

        // If written directly in rich text
        p {
            margin-bottom: 15px;
            &:last-child {
                margin-bottom: 0;
            }
        }

        // If written with ordered list option in rich text
        ol {
            counter-reset: item;
            margin: 0;
            padding-left: 0;
            li {
                counter-increment: item;
                list-style-type: none;
                margin-bottom: 15px;
                text-indent: -9px;
                &::before {
                    content: counter(item) '.';
                    display: inline-block;
                    font-weight: bold;
                    padding-right: 3px;      
                    text-align: right;
                    width: 1.5rem;
                  }
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}