.codeHighlighter {
  border: 1px solid $grey;

  &__component {
    align-items: center;
    display: flex;
    justify-content: center;
    min-height: 200px;
    padding: 30px;
    position: relative;

    .component {
      width: 100%;
    }

    .show-code {
      // background-color: $subtle-grey;
      // border: 1px solid $medium-grey;
      border-radius: 50%;
      bottom: 20px;
      // color: $medium-grey;
      height: 45px;
      letter-spacing: 2px;
      padding: 0;
      position: absolute;
      right: 20px;
      width: 45px;

      &:active {
        transform: scale(.92);
      }
    }
  }

  &__code {
    border-top: 1px solid $grey;
    display: none;
    padding: 30px;
    &.show {
      display: block;
    }
    .nav-tabs {
      padding: 8px 0;
      li {
        position: relative;
        &:hover, &:focus {
          &::after {
              background-color: $bell-blue;
              bottom: -8px;
              content: '';
              display: block;
              height: 4px;
              position: absolute;
              width: 100%;
          }
        }
        &.active {
            &::after {
                background-color: $bell-blue;
                bottom: -8px;
                content: '';
                display: block;
                height: 4px;
                position: absolute;
                width: 100%;
            }
        }
        &:not(:first-child) {
          margin-left: 30px;
          margin-top: 0;
        }
        &::after {
          display: none;
        }
        &:hover, &.active {
          a {
            text-decoration: none;
          }
        }
        a {
          color: $black;
          &.active {
            &::after {
              display: none;
            }
          }
        }
      }
    }
  
     .tab-content {
       margin-top: 50px;
       pre {
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
        margin-top: 0;
          // &::-webkit-scrollbar {
          //   width: 6px;
          // }
          // &::-webkit-scrollbar-track {
          //   background: $light-grey;
          //   border-bottom-left-radius: 3px;
          //   border-bottom-right-radius: 3px;
          // }
          // &::-webkit-scrollbar-thumb {
          //   background: $bell-blue;
          //   border-radius: 3px;
          // }
       }
     }
  }
}