.audio-player {
    &__bg {
        align-items: center;
        background-color: $subtle-grey;
        border-radius: 6px;
        display: flex;
        height: 100px;
        justify-content: center;
        position: relative;
        width: 100%;

        .waves-bg {
            background: url("../images/waves.png") no-repeat center center;
            background-size: 100%;
            height: 100%;
            position: absolute;
            width: 100%;
        }
    }
    &__button {
        background-color: $bell-blue;
        border: none;
        border-radius: 50%;
        cursor: pointer;
        height: 72px;
        position: relative;
        width: 72px;
    }
    .is-paused {
        outline: none;
        &::after {
            color: $white;
            content: $icon-play;
            font-family: $icomoon-font-family;
            font-size: 37px;
        } 
    }
    .is-playing {
        outline: none;
        &::after {
            color: $white;
            content: $icon-pause;
            font-family: $icomoon-font-family;
            font-size: 37px;
        }
    }
    &__caption {
        color: $medium-grey;
        font-size: 12px;
        margin-top: 15px;
    }
}