.image-card {
    .card-title {
        font-family: $bell-slim-black;
        margin-bottom: 0;
        //margin-left: -7px;
    }
    .card-text {
        align-items: center;
        color: $white;
        display: flex;
        font-size: 14px;
        font-weight: bold;
        justify-content: center;
        span {
            font-weight: bold;
        }
    }
    .card-note {
        padding: 28px 5px 0 5px;
    }
    &__icon {
        font-size: 24px;
        margin-left: 6px;
        position: relative;
        top: 1px;
    }
}