.image-component {
    position: relative;

    &__image {
        background: #efefef;
        // overflow: hidden;
    }

    &--border {
        align-items: center;
        border: 1px solid $grey;
        display: flex;
        justify-content: center;

        img {
            // display: block;
            // margin-right: auto;
            // margin-left: auto;
        }
    }

    &--shadow {
        img {
            box-shadow: 0 6px 25px 0 rgba(0,0,0,0.12);
        }
    }
    
    &.zoom-image {
        a {
            display: inline-block;
            position: relative;
            
            &::after {
                color: $bell-blue;
                content: $icon-search;
                font-family: $icomoon-font-family;
                font-size: 30px;
                left: 50%;
                margin: -15px 0 0 -15px;
                opacity: 0;
                pointer-events: none;
                position: absolute;
                top: 50%;
                transform: translateY(10px);
                transition: $default-animation;
                width: 30px;
                z-index: 5;
            }
            &:hover {
                &::after {
                    opacity: 1;
                    transform: translateY(0);
                }
                .figure-img {
                    opacity: .75;
                }
            }
        }
        
        &--border {
            a {
                display: block;
            }
        }
        
        .image-component--border {
            transition: $default-animation;
            a {
                display: block;
            }
            &:hover {
                box-shadow: 0 10px 16px 0 rgba(0,0,0,0.2);
            }
            .figure-caption {
                text-align: left;       
            }
        }
    }

    &+ .image-component {
        margin-top: 17px;
    }
}

figure {
    margin-bottom: 0;
    .figure-img {
        margin-bottom: 0;
    }
    .figure-caption {
        margin-top: .875em;
    }
}