// Color system
$black: #111111;

$body-color: $black;

$theme-colors: (
    "bell-blue": $bell-blue,
    "medium-blue": $medium-blue,
    "dark-blue": $dark-blue,
    "light-blue": $light-blue,
    "dark-grey": $dark-grey,
    "medium-grey": $medium-grey,
    "light-grey-text": $light-grey-text,
    "grey": $grey,
    "light-grey": $light-grey,
    "subtle-grey": $subtle-grey,
    "red": $red,
    "primary": $medium-blue,
    "secondary": transparent,
);

// Components
$border-radius-lg: 0.375rem;

// Alerts
$alert-border-width: none;

// Buttons
$btn-font-size: 15px;
$btn-padding-y: 0.6875rem;
$btn-padding-x: 1.875rem;
$btn-border-radius: 20px;
$btn-line-height: 1;
$btn-font-weight: normal;

// Cards
$card-border-color: #E1E1E1;
$card-border-radius: $border-radius-lg;
$card-border-width: 1px;

// Grid
// $grid-gutter-width: 0.9375rem;

// Dropdowns
$enable-caret: false;

// Forms
$form-feedback-icon-invalid: none;
$form-feedback-icon-valid: none;
$form-feedback-icon-invalid-color: $red;
$form-feedback-invalid-color: $red;
$form-feedback-valid-color: $grey;
$input-bg: $subtle-grey;
$input-border-color: $grey;
$input-border-radius: 0px;
$input-border-width: 2px;
$input-color: $medium-grey;
$input-font-size: pxToRem(14px);
$input-padding-y: .78125rem;
$input-padding-x: .9375rem;
$input-btn-focus-color: none;

// Images
$figure-caption-font-size: .75em;
$figure-caption-color: $medium-grey;

// List group
$list-group-color: $bell-blue;
$list-group-border-color: none;
$list-group-hover-bg: none;
$list-group-active-color: $dark-grey;
$list-group-active-bg: none;
$list-group-action-active-color: $dark-grey;
$list-group-action-active-bg: none;

// Modal
$modal-md: 645px;
$modal-content-border-color: $light-grey;
$modal-content-border-width: 1px;
$modal-content-border-radius: 10px;
$modal-content-box-shadow-sm-up: 0 14px 36px 0 rgba(0,0,0,0.3);
$modal-header-border-width: 0px;
$modal-footer-border-width: 0px;

// Navs
$navbar-dark-color: #C2CEDF;
$navbar-dark-hover-color: #FFFFFF;
$navbar-dark-toggler-border-color: none;

// Tabs
$nav-tabs-border-color: none;
$nav-tabs-link-hover-border-color: none;
$nav-tabs-link-active-border-color: none;
$nav-tabs-link-active-color: $black;

// Tables
$table-accent-bg: $subtle-grey;
$table-border-color: $grey;
$table-border-width: 1px;
$table-cell-padding: 1.3rem 1.5rem;
$table-color: $medium-grey;
$table-striped-order: even;

// Typography
$font-family-base: Arial, Helvetica, sans-serif;
$text-muted: $medium-grey; // color from bell.ca, initially #666 on invision