.table > thead > tr > th {
    padding: 1rem;

    border-color: $bell-blue;
    border-right-color: $dark-blue;

    &:last-child {
        border-right-color: $bell-blue;
    }
}

.table > thead > tr > th, 
.table > tbody > tr > td, 
.table > tbody > tr > th {
    font-size: 14px;
    vertical-align: middle;
}