.do-dont {
    &__image {
        .card-img-overlay {
            margin-bottom: 20px;
        }
    }

    &__title {
        font-size: 14px;
        font-weight: bold;
        margin-top: 20px;

        &::after {
            content: '';
            display: block;
            height: 5px;
            margin-top: 5px;
            width: 100px;
        }
    }

    &__text {
        color: $medium-grey;
        line-height: 18px;
        margin: 22px 0;

        p {
            font-size: 14px;
        }

        ol, ul {
            color: $medium-grey;
            margin-left: 6px;
        }

        ul > li {
            &::before {
                color: $medium-grey !important;
                left: -6px !important;
            }
        }
    }

    &--do {
        color: $green;

        &::after {
            background-color: $green;
        }
    }

    &--dont {
        color: $red;

        &::after {
            background-color: $red;
        }
    }
}