.btn-primary {
    border: 2px solid $medium-blue;
    &:hover {
        background: $bell-blue;
        border-color: $bell-blue;
    }
    &:disabled {
        background: $light-grey-disabled;
        border-color: $light-grey-disabled;
    }
}

.btn-secondary {
    border: 2px solid $medium-blue;
    color: $medium-blue;
    &:hover {
        border-color: $bell-blue;
        color: $bell-blue;
    }
    &.disabled {
        border-color: $light-grey-disabled;
        color: $grey-disabled;
    }
}