.nav-tabs {
    padding-bottom: 0;

    display: inline-block;

    .nav-item {
        display: inline-block;

        &:not(:last-child) {
            margin-right: 30px;
            margin-top: 0;
        }
        &::before {
            content: '';
        }
    }

    .nav-link {
        align-items: center;
        color: $black;
        display: inline-flex;
        flex-direction: column;
        padding: 15px 0;
        font-size: 18px;
        position: relative;
        text-decoration: none;
        &:hover {
            color: $black;
        }

        &::before {
            content: attr(data-text);
            content: attr(data-text) / "";
            height: 0;
            visibility: hidden;
            overflow: hidden;
            user-select: none;
            pointer-events: none;
            font-weight: bold;
            @media speech {
                display: none;
              }
        }
    }

    .active {
        font-weight: bold;

        &::after {
            background-color: $bell-blue;
            bottom: 1px;
            content: '';
            display: block;
            height: 4px;
            position: absolute;
            width: 100%;
        }
    }
}

.nav-tabs > .nav-item > .nav-link {
    background: transparent;
    border: none;

    &:hover, &:focus {
        &::after {
            background-color: $bell-blue;
            bottom: 1px;
            content: '';
            display: block;
            height: 4px;
            position: absolute;
            width: 100%;
        }
    }
}

// Priority nav
.priority-nav {
    border-bottom: 1px solid #E1E1E1;
    white-space: initial;
    @include media-breakpoint-down(sm) {
        border-bottom: none;
    }
    &.is-open {
        .icon-arrow-down {
            top: 5px;
            transform: rotate(180deg);
        }
    }
    &__wrapper {
        font-size: 18px;
        line-height: 26px;
        padding-bottom: 18px;
        padding-left: 61px;
        padding-right: 40px;
        position: relative;

        .icon-arrow-down {
            color: $bell-blue;
            font-size: 9px;
            right: 20px;
            pointer-events: none;
            position: absolute;
            top: 7px;
        }

        &::before {
            background: $grey;
            content: '';
            display: block;
            height: 24px;
            left: 41px;
            position: absolute;
            top: 0;
            width: 1px;
        }

        &[aria-haspopup="false"] {
            .icon-arrow-down {
                display: none;
            }
            &::before {
                display: none;
            }
        }

        &.active {
            button {
                color: $black;
                // max-width: 112px;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            &::after {
                background: $bell-blue;
                bottom: 0px;
                content: '';
                display: block;
                height: 4px;
                left: 66px;
                position: absolute;
                width: 90px;
            }
        }

        button {
            background: transparent;
            border: none;
            color: $bell-blue;
            position: relative;
            text-transform: capitalize;
            width: 76px;
            white-space: nowrap;
            &:focus {
                outline: none;
            }
        }
    }

    &__dropdown {
        background: $white;
        border: 1px solid $light-grey;
        box-shadow: 0 14px 36px 0 rgba(0,0,0,0.3);
        list-style: none;
        padding: 10px;
        left: 50%;
        top: calc(100% + 20px);
        transform: translateX(-50%);
        width: 400px;
        z-index: 9999;
        a {
            border-radius: 3px;
            color: $black;
            line-height: 26px;
            font-size: 16px;
            padding: 8px 10px;
            &:hover {
                background: $light-grey;
            }
            &[aria-selected="true"] {
                font-weight: bold;
                text-decoration: underline;
            }
        }
        &::before {
            content: '';
            border-left: 15px solid transparent;
            border-right: 15px solid transparent;
            border-bottom: 15px solid $border-color;
      
            bottom: 100%;
            height: 0;
            left: 50%;
            pointer-events: none;
            position: absolute;
            transform: translateX(-50%);
            width: 0;
            z-index: 2;
        }
        &::after {
            content: '';
            border-left: 14px solid transparent;
            border-right: 14px solid transparent;
            border-bottom: 14px solid $white;
      
            bottom: 100%;
            height: 0;
            left: 50%;
            pointer-events: none;
            position: absolute;
            transform: translateX(-50%);
            width: 0;
            z-index: 2;
          }
    }

    @include media-breakpoint-down(sm) {
        &__wrapper {
            border-bottom: none;
            padding: 0;
        }
        &__dropdown-toggle {
            display: none;
        }
        &__dropdown {
            border: none;
            box-shadow: none;
            left: unset;
            margin-top: -51px;
            padding: 0 0 25px 0;
            position: relative;
            transform: none;
            visibility: visible;
            width: auto;
            z-index: 1;
            &::before {
                border: none;
            }
            &::after {
                background: $light-grey;
                border: none;
                bottom: 0;
                content: '';
                height: 1px;
                left: 0;
                position: absolute;
                width: 204%;
            }
            .nav-item {
                &:not(:last-child) {
                    margin-bottom: 15px;
                }
                .nav-link {
                    color: $bell-blue;
                    display: inline;
                    font-size: 14px;
                    line-height: 26px;
                    padding: 0;
                    text-decoration: underline;
                    &:hover {
                        background: transparent;
                        color: $medium-blue;
                    }
                    &[aria-selected="true"] {
                        color: $black;
                        font-weight: bold;
                        text-decoration: none;
                    }
                }
            }
        }
    }
}