// Essaye padding sur ul ou li, cibler first/last-child

.documentation-sidebar {
  background: $white;
  border-right: 1px solid $border-color;
  height: calc(100vh - 4rem);
  left: 0;
  overflow-x: hidden;
  padding-bottom: 30px;
  position: fixed;
  top: 4rem;
  width: 300px;
  z-index: 9;


  &::-webkit-scrollbar {
    width: 6px;
  }
  &::-webkit-scrollbar-track {
    background: $light-grey;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: $bell-blue;
    border-radius: 3px;
  }

  &__list {
    background-color: $subtle-grey;
    border-bottom: 1px solid $light-grey;
    > li {
      position: relative;

      &:not(:first-child) {
        border-top: 1px solid $border-color;
      }

      &::before {
        content: "";
        @include gradient-y(rgba(232,232,232,0), rgba(223,223,223,.36));
        opacity: 0;
        pointer-events: none;
        position: absolute;
        left: 0;
        top: -10px;
        height: 10px;
        width: 100%;
      }

      > a {
        align-items: center;
        color: $bell-blue;
        display: flex;
        justify-content: space-between;
        padding: 20px 15px;
        position: relative;
        z-index: 5;

        &::after {
          color: $bell-blue;
          content: $icon-arrow-right;
          display: inline-block;
          font-family: $icomoon-font-family;
        }

        &:hover {
          color: $black;
          text-decoration: none;
        }
  
        &[aria-expanded="true"] {
          background-color: $white;

          &::after {
            color: $bell-blue;
            content: $icon-arrow-up;
            display: inline-block;
            font-family: $icomoon-font-family;
          }
        }

        &[aria-expanded="false"] {
          &::after {
            color: $bell-blue;
            content: $icon-arrow-down;
            display: inline-block;
            font-family: $icomoon-font-family;
          }
        }
      }

      &.active {
        background-color: $white;
        border-right-color: $white;
        box-shadow: 0 6px 25px 0 rgba(0,0,0,0.12);
        &::before {
          opacity: 1;
        }

        > a {
          color: $black;
        }
      }
    }
  }

  &__submenu {
    background-color: $white;
    position: relative;
    z-index: 1;

    > .nav-item {
      padding-left: 15px;
      &:last-child {
        padding-bottom: 15px;
      }

    }

    a {
      align-items: center;
      color: $bell-blue;
      display: flex;
      font-size: 14px;
      justify-content: space-between;
      padding: 10px 15px 10px 10px;

      &[aria-expanded="true"] {
        background-color: $white;

        &::after {
          color: $bell-blue;
          content: $icon-arrow-up;
          display: inline-block;
          font-family: $icomoon-font-family;
          font-size: 16px;
        }
      }

      &[aria-expanded="false"] {
        &::after {
          color: $bell-blue;
          content: $icon-arrow-down;
          display: inline-block;
          font-family: $icomoon-font-family;
          font-size: 16px;
        }
      }

      &:hover {
        text-decoration: none;
      }
    }

    .active {
      a {
        color: $black;
        font-weight: bold;
      }
    }

    // Blue bar = height 30px in Invision, but if I want my a centered with it, it needs to be 41px like my li.
    &--level3 {
      
      box-shadow: none;
      // margin: 12px 0;
      // padding-top: 0;
      // padding-bottom: 0;

      > li {
        padding-left: 12px;
        
        a {
          // !important because .active > a above is affecting every level3 links
          border-left: 1px solid $border-color;
          color: $bell-blue !important;
          font-weight: normal !important;
          line-height: 1;
          padding: 13px 15px 13px 16px;
          position: relative;
        }
        &.active {
          a {
            color: $black !important;
            font-weight: bold !important;
            &::before {
              background-color: $bell-blue;
              content: '';
              display: block;
              height: 100%;
              left: 0;
              position: absolute;
              top: 0;
              width: 4px;
            }
          }
          
        }
      }
    }
  }
}
.sidenav {
  display: none;
}

@media(max-width: 991px) {
  .documentation-sidebar {
    display: none;
  }

  .sidenav {
    display: block; 

    &__content {
      background-color: $white;
      display: block;
      overflow-x: hidden;
      padding-bottom: 30px;
      position: fixed;
      right: 0;
      transition: $default-animation;
      transform: translateX(100%);
      width: 300px;
      z-index: 9999;

      &--level1 {
        height: calc(100% - 4rem);
        bottom: 0;
      }
      &--level2 {
        height: 100%;
      }
    }
  }

  .blocker--level1, .blocker--level2 {
    bottom: 0;
    content: '';
    display: none;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 9998;
  }

  &.show {
    .sidenav__content--level1, .sidenav__content--level2 {
      transform: translateX(0);
    }
    .blocker--level1, .blocker--level2 {
      display: block;
    }
  }
}