.quick-links {
    &__content {
        padding: 100px 0;
        a:hover {
            text-decoration: none;
        }
    }

    &__image-section {
        background: $white;
        border-radius: 50%;
        height: 116px;
        margin-bottom: 76px;
        position: relative;
        width: 116px;
    }

    &__image {
        max-width: 82px;
    }

    &__download-icon {
        background-color: $bell-blue;
        border-radius: 50%;
        bottom: -4px;
        color: $white;
        cursor: pointer;
        font-size: 21px;
        left: 0;
        padding: 8px;
        position: absolute;
    }

    &__heading {
        color: $dark-grey;
        font-family: $bell-slim-black;
        font-size: 20px;
        margin-bottom: 13px;
    }

    &__note {
        color: $medium-grey;
        font-size: 14px;
        line-height: 18px;
        margin-bottom: 75px;
        padding: 0 20px;
    }
}

@media(max-width: 767px) {
    .quick-links {
        .container {
            padding-top: 15px;
        }
        &__content {
            padding: 15px 0;
        }
    }
}