.form {
  label {
    color: $black;
    font-size: 14px;
    padding-right: 9px;
  }

  .row {
    margin-bottom: 0;
    padding-bottom: 0;
  }

  .was-validated .form-control:invalid, .form-control.is-invalid {
    background-size: 20px 20px;
  }

  .was-validated .form-control:valid, .form-control.is-valid {
    background-size: 20px 20px;
  }

  &__button {
    margin-top: 1rem;
    min-width: 147px;
  }

  &__errors {
    color: $red;
    font-size: 12px;
    list-style: none;
    margin: 10px 0;
  }

  &__error-icon {
      height: 16px;
      margin-right: 8px;
      width: 16px;
  }
}

.file-drop-area {
    align-items: center;
    border: 2px dashed $grey;
    display: flex;
    flex-direction: column;
    height: 175px;
    justify-content: center;
    max-width: 100%;
    padding: 25px;
    position: relative;
    transition: 0.2s;
    &.is-active {
      background-color: $subtle-grey;
    }
    &.is-invalid {
      border-color: $red;
    }
  }
  
.fake-btn {
  background-color: transparent;
  border: 2px solid $medium-blue;
  border-radius: 20px;
  color: $medium-blue;
  line-height: 1;
  font-size: 15px;
  padding: 0.6875rem 2.25rem;
}
  
.file-msg {
  color: $black;
  line-height: 1.4;
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
  
.file-input {
  cursor: pointer;
  left: 0;
  height: 100%;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
  &:focus {
    outline: none;
  }
}

.success-msg {
  padding: 30px 52px 30px 81px;
  position: relative;

  &__heading {
    color: $black;
    font-size: 18px;
    margin-bottom: 12px;
  }

  &__body {
    color: $medium-grey;
    font-size: 14px;
    p:last-child {
      margin-bottom: 0;
    }
  }

  &__icon {
    color: green;
    font-size: 36px;
    // height: 40px;
    left: 28px;
    position: absolute;
    top: 28px;
    // width: 40px;

    // & + .alert__body {
    //     margin-top: 9px;
    // }
  }
}