a.link-with-icon  {
    color: $bell-blue;
    display: inline-block;
    font-size: 14px;
    padding-right: 40px;
    position: relative;
    text-decoration: none;
    min-width: 150px;

    i {
        font-size: 24px;
        margin-top: -11px;
        position: absolute;
        right: 0;
        top: 50%;
    }

    &:hover,
    &:focus {
        text-decoration: underline;
    }
}