.main-nav {
    background-color: $bell-blue;
    height: 100vh;
    left: 0;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 300px;

    &__logo {
        a {
            border-bottom: 1px solid $medium-blue;
            display: flex;
            padding: 30px 0 34px 20px;
            img {
                width: 68px;
            }
    
            span {
                color: $white;
                font-family: $bell-slim, serif;
                font-size: 22px;
                font-weight: bold;
                letter-spacing: -1.1px;
                line-height: 22px;
                margin-left: 15px;
            }
            &:hover {
                text-decoration: none;
            }
        }
    }

    &__menu {
        li {
            border-bottom: 1px solid $medium-blue;
            a {
                color: $light-blue;
                display: flex;
                justify-content: space-between;
                font-size: 20px;
                font-family: $bell-slim;
                padding: 14px 26px 14px 20px;
                position: relative; // because box-shadow goes under div otherwise
                transition: $default-animation;

                &::after {
                    color: $light-blue;
                    content: '';
                    display: inline-block;
                    font-family: $icomoon-font-family;
                    left: 262px;
                    position: absolute;
                    @include media-breakpoint-down(md) {
                        left: calc(100% - 19px);
                        transform: translateX(-100%);
                        // left: auto;
                        // transform: translateX(-100%);
                        // right: 50px;
                    }
                }

                &[aria-expanded="true"] {
                    box-shadow: inset 0 11px 17px 0 rgba(0,0,0,.11);  
                    &::before {
                        content: '';
                        border-left: 8px solid transparent;
                        border-right: 8px solid transparent;
                        border-top: 8px solid $bell-blue;

                        height: 0;
                        width: 0;
                        pointer-events: none;
                        position: absolute;
                        top: 100%;
                        left: 16px;
                        z-index: 2;
                    }    
                    &::after {
                      content: $icon-arrow-up;
                    }
                }
                &[aria-expanded="false"] {          
                    &::after {
                      content: $icon-arrow-down;
                    }
                }
            }

            &.active {
                .nav-link--level1 {
                    color: $white;
                    font-weight: bold;
                    // font-family: $bell-slim-black;
                }
            }
        }
    }

    &__submenu {
        background-color: $medium-blue;
        li {
            border-bottom: 1px solid #0B2861;
            a {
                color: $light-blue;
                font-family: Arial, serif;
                font-size: 14px;
                &.nav-link--level2 {
                    padding: 20px 30px 20px 20px;
                }
                &:hover {
                    text-decoration: none;
                }
                &::after {
                    content: '';
                    left: 268px;
                    @include media-breakpoint-down(md) {
                        left: calc(100% - 15px);
                        transform: translateX(-100%);
                    }
                }
                &[aria-expanded="true"] {
                    &::before {
                        content: '';
                        border-top: 8px solid $medium-blue;
                        left: 18px;
                    }
                }
            }

            &.active {
                .nav-link--level2 {
                    color: $white;
                    font-weight: bold;
                }
            }
        }

        &--level2 {
            background-color: $dark-blue;
            li {
                border-bottom: none;
                a {
                    font-size: 14px;
                    &.nav-link--level3 {
                        padding: 15px 30px 15px 20px;
                        &[aria-expanded="true"] {
                            box-shadow: none;
                            &::before {
                                content: '';
                                border: none;
                            }
                        }
                    }
                }
                &.active {
                    .nav-link--level3 {
                        color: $white;
                        font-weight: bold;
                    }
                }
            }
        }

        &--level3 {
            background-color: $dark-blue;
            li {
                &:first-child {
                    a {
                        padding-top: 25px;
                    }
                }
                &:last-child {
                    a {
                        padding-bottom: 15px;
                    }
                }
                a {
                    font-size: 14px;
                    margin-left: 19px;
                    padding-top: 10px;
                    padding-bottom: 10px;
                }
                &.active {
                    .nav-link--level4 {
                        color: $white;
                        font-weight: bold;
                    }
                }
            }
        }
    }

    &::-webkit-scrollbar {
        width: 6px;
    }
    &::-webkit-scrollbar-thumb {
        background: $dark-blue;
        border-radius: 3px;
    }
    @include media-breakpoint-up(lg) {
        &::-webkit-scrollbar-track {
            background: $light-grey;
            border-radius: 3px;
        }
    }
}

@media(max-width: 991px) {
    .main-nav {
        height: calc(100vh - 70px);
        top: 70px; // navbar height (mobile)
        transform: translateY(-100%);
        transition: $default-animation;
        width: 100%;
        z-index: 150;

        &.show {
            transform: translateY(0);
        }

        &__logo {
            display: none;
        }
    }
}