.swatch {
    &__colour {
        border-radius: 6px;
        color: $white;
        display: flex;
        flex-direction: column;
        font-family: $bell-slim, sans-serif;
        font-size: 24px;
        font-weight: bold;
        height: 80px;
        justify-content: center;
        margin-bottom: 15px;
        padding-left: 30px;

        &--border {
            border: 1px solid $light-grey;
        }
    }

    &__name {
        color: $medium-grey;
        font-size: $font-size-sm;
        font-weight: bold;
    }

    &__text {
        color: $medium-grey;
        p {
            font-size: 14px;
        }
    }

    &__note {
        p {
            color: $medium-grey;
            font-size: 14px;
            a {
                text-decoration: underline;
            }
        }
    }

    // Large
    &--large {
        .swatch__colour {
            align-items: center;
            border-radius: 10px;
            font-family: 'Arial Bold', sans-serif;
            height: 154px;
            padding-left: 0;
        }
    }

    .dark-text {
        color: $medium-grey;
    }
}