.icons-filter {
    &__search-section {
        align-items: center;
        display: flex;
        justify-content: space-between;
        padding-bottom: 5px;

        .search-form {
            margin-right: 15px;
            position: relative;
            input {
                border: 2px solid $grey;
                border-radius: 18px;
                color: $black;
                font-size: 14px;
                min-width: 240px;
                padding: 7px 48px 7px 15px;
                &::placeholder {
                    color: $light-grey-text;
                }
                &:focus {
                    border: 2px solid #96B8EF;
                    outline: none;
                }
            }
            &__reset-icon {
                background: transparent;
                border: none;
                border-radius: 50%;
                display: none;
                margin-top: -12px;
                position: absolute;
                right: 20%;
                top: 50%;

                i {
                    font-size: 12px;
                }

                &.active {
                    display: block;
                }
            }
            &__search-icon {
                background: transparent;
                border: none;
                position: absolute;
                right: 7px;
                top: calc(50% - 13px);
                i {
                    color: $bell-blue;
                    cursor: pointer;
                    font-size: 26px;
                }
            }
        }

        a {
            align-items: center;
            display: flex;
            font-size: 14px;
            font-weight: bold;
            justify-content: center;
            text-decoration: none;
            &:hover {
                color: $bell-blue !important;
            }
        }

        .icon-download {
            background-color: $bell-blue;
            border-radius: 50%;
            color: $white;
            font-size: 30px;
            margin-left: 15px;
            padding: 5px;
        }
    }

    &__accordions-section {
        .card-header {
            h2 {
                margin-bottom: 0;
            }
        }

        .card-body {
            padding-right: 0 !important; // BECAUSE ACCORDION.SCSS STYLES ALL ACCORDIONS
            h3 {
                color: $black;
                margin-bottom: 0;
                padding-bottom: 30px;
                &:not(:first-child) {
                    padding-top: 60px;
                }
            }
        }

        .row-icons {
            // max-width: 100%;
            // margin-left: -15px;
            // margin-right: -15px;
            margin-right: -30px;
            .block-icon {
                margin-bottom: 60px;

                margin-right: 30px;

                // padding-right: 15px;
                // padding-left: 15px;
                &.small-icons {
                    // flex: 0 0 8%;
                    // max-width: 8%;
                    width: 76px;
                }
                &.normal-icons {
                    // flex: 0 0 13%;
                    // max-width: 13%;
                    width: 130px;
                }
                &.large-icons {
                    // flex: 0 0 22%;
                    // max-width: 22%; 
                    width: 210px;
                }

                &--download {
                    &.small-icons, &.normal-icons {
                        // flex: 0 0 13%;
                        // max-width: 13%;
                        width: 130px;
                    }
                    &.large-icons {
                        // flex: 0 0 22%;
                        // max-width: 22%; 
                        width: 210px;
                    }  
                }
            }
        }

        .icon {
            align-items: center;
            display: flex;
            justify-content: center;
            min-height: 36px;

            img {
                min-width: 16px;
            }

            &.downloadGroupIcons {
                border-radius: 0;
                background-color: $subtle-grey;
                height: 100%;
                min-height: 68px;
                min-width: 80px;
                padding: 0 29px;
                text-decoration: none;
                .btn {
                    border: 2px solid $medium-blue;
                    color: $medium-blue;
                    font-size: 15px;
                    padding: 11px calc(25% - 4px);
                }
                @include media-breakpoint-down(sm) {
                    padding: 0 24px;
                    .btn {
                        font-size: 12px;
                        padding: 11px 14px;
                    }
                }
            }
        }
    
        .caption {
            color: $medium-grey;
            display: block;
            font-size: 12px;
            margin-top: 14px;
            text-align: center;
        }

        .with-border {
            .icon {
                border: 1px solid $light-grey;
                padding: 29px;
            }
            .downloadGroupIcons {
                border: none;
            }
        }
    }
}