h1, .h1,
h2, .h2,
h3, .h3 {
  font-family: $bell-slim-black;
  font-weight: normal;
}

h1 {
  font-size: 40px;
  letter-spacing: -0.7px;
  line-height: 46px;
  margin-bottom: 25px;
}

h2 {
  font-size: 32px;
  letter-spacing: -0.5px;
  line-height: 38px;
  margin-bottom: 20px;
}

h3 {
  font-size: 24px;
  letter-spacing: -0.4px;
  line-height: 26px;
  margin-bottom: 20px;
}

h4 {
  font-family: $font-family-base;
  font-size: 18px;
  font-weight: bold;
  line-height: 22px;
  margin-bottom: 20px;
}

h5 {
  font-family: $font-family-base;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 20px;
}

h6 {
  font-family: $font-family-base;
  font-size: 14px;
  font-weight: bold;
  line-height: 18px;
  margin-bottom: 20px;
}

p {
  color: $medium-grey;
    font-size: .875em;
}

.lead {
  color: $black;
  font-family: $font-family-base;
  font-size: 1.125em;
  //line-height: 25px;
  margin-bottom: 20px;
}

.documentation__body {
  .rich-text {
    a {
      color: $bell-blue;
      text-decoration: underline;
      &:hover {
        color: $medium-blue;
      }
    }
    
    ul {
      color: $medium-grey;
      font-size: 14px;
      list-style: none;
      padding-left: 0;
      
      li {
        line-height: 18px;
        padding-left: 20px;
        position: relative;
        &::before {
          color: $bell-blue;
          content: '\2022';
          font-size: 18px;
          position: absolute;
          left: 2px;
        }
        &:not(:first-child) {
          margin-top: 15px;
        }
        > ul {
          // margin-left: -20px;
          margin-top: .5em;
          // padding-left: 45px;
          position: relative;
          li::before {
            color: $medium-grey;
            content: '-';
            position: absolute;
            //left: 25px;
          }
        }
      }
    }
    p {
      max-width: 960px;
    }
  }

  div[class^="col"] > p {
    padding-right: pxToRem(30px);
  }
  .lead {
    padding: pxToRem(15px) 0;
    &:first-child {
      padding-top: 0;
    }
  }
}


.smaller-para {
  font-size: 14px;
  line-height: 18px;
}

.footer-para {
  font-size: 12px;
}