.card {
    transition: $default-animation;
    &--clickable {
        &:hover {
            box-shadow: 0 20px 25px 0 rgba(0,0,0,0.2);
        } 
    }
    &--shadow {
        box-shadow: 0 6px 25px rgba(0,0,0,0.12);
    }

    &-img {
        height: auto;
    }

    &-img-top {
        background-color: $subtle-grey;
        border-bottom: 1px solid $border-color;
    }

    &__withIcon {
        background-color: $subtle-grey;
        border-bottom: 1px solid $border-color;
        padding: 52.5px 0;

        .card-img-top {
            border: none;
        }
        img {   
            height: 72px;
        }
    }

    &-body {
        .card-title {
            font-size: 20px;
        }
        p {
            color: $medium-grey;
            font-size: 0.875rem;
        }
    }

    div[class^="col"] & {
        height: 100%;
    }
}