.documentation {
  margin-left: 300px;
  // min-height: calc(100vh - 114px); // minus footer height
  padding-bottom: pxToRem(60px);
  width: calc(100% - 300px);

  &__section {
    padding-top: pxToRem(60px);

    &:first-child {
      padding-top: pxToRem(45px);
    }
    
    // Rows in main content 
    .row {
      &--noPadding {
        padding: 0;
      }
      &--normalPadding {
        padding: 1rem 0;
      }
      &--mediumPadding {
        padding: 40px 0;
      }
      &--largePadding {
        padding: 60px 0;
      }
    }
    .documentation__heading + .row {
      padding-top: 0;
    }

    // Columns in main content
    .row > div[class^="col"] {
      margin-bottom: .9375rem;
    }
  }

  @include media-breakpoint-down(md) {
    margin-left: 0;
    width: 100%;
  }

  @include media-breakpoint-up(md) {
    .container-fluid {
      padding-right: pxToRem(30px);
      padding-left: pxToRem(30px);
    }
  }

  @include media-breakpoint-up(xl) {
    .container-fluid {
      margin-right: unset;
      margin-left: unset;
      max-width: 1226px;
      padding-right: pxToRem(60px);
      padding-left: pxToRem(60px);
    }
  }
}

