.media {
    &:not(:first-child) {
        margin-top: 30px;
    }

    &__icon {
        background-color: #999999;
        border-radius: 50%;
        color: #FFFFFF;
        // font-family: $bell-slim-black;
        font-size: 20px;
        height: 30px;
        line-height: 30px;
        margin-right: 15px;
        text-align: center;
        text-transform: uppercase;
        width: 30px;
    }

    .media-body {
        margin-top: 9px;
    }

    &__title {
        color: $black;
        font-family: $bell-slim-black;
        font-size: 24px;
        margin-bottom: -8px;
    }

    &__content {
        color: $medium-grey;
        //font-size: 14px;

        a {
            color: $bell-blue;
            text-decoration: underline;

            &:hover {
                color: $medium-blue;
            }
        }
    }
}